import Vue from 'vue'
import Vuex from 'vuex'
// import permission from './module/permission'
import useDate from './module/useDate'
import getters from './getter'

Vue.use(Vuex)

const store = new Vuex.Store({
    modules: {
        // permission
        useDate
    },
    getters
});

export default store


