<template>
        <el-dialog center="true" :visible.sync="showUserInfoFlag" class="dialog">
             <div class="addMsg">
            <el-form class="formArea" :label-position="labelPosition" :rules="formRules" ref="ValidateForm" :model="formLabelAlign"  label-width="100px" >
                <el-form-item prop="Company" label="公司名称"> 
                    <el-input  v-model="formLabelAlign.Company"></el-input>
                </el-form-item>
                <el-form-item prop="VinCode" label="底盘号">
                    <el-input  v-model="formLabelAlign.VinCode"></el-input>
                </el-form-item>
                <el-form-item prop="DateOnCard" label="上牌日期" >
                    <el-date-picker 
                        v-model="formLabelAlign.DateOnCard"
                        type="datetime"
                        placeholder="选择日期时间"
                        style="width:100%">
                        </el-date-picker>
                </el-form-item>
                <el-form-item style="text-align:center;margin-left:-100px">
                   <el-button type="primary" @click.native.prevent="handleSubmit">行驶证信息提交</el-button>
                </el-form-item>
            </el-form>
            </div>
        </el-dialog>
       

</template>
<script>
import {CompleteInfor} from "@/axios/login.js";
export default {
    props: ['allRules'],
  data() {
    const validateVinCode = (rule, value, callback) => {
      if (value.length < 1) {
        callback(new Error("请输入17位底盘号"));
      }else if(value.replace(/\ +/g,"").length!=17){
          callback(new Error("请输入17位底盘号"))
      }
       else {
        callback();
      }
    };

    return {
    showUserInfoFlag: false,
    labelPosition:'right',
      formLabelAlign: {
        Company: "",
        VinCode: "",
        DateOnCard: "",
        ID:''
      },
      formRules: {
        Company: [
          { required: true,message:'请填写公司名称' }
        ],
        VinCode: [{ required: true, validator: validateVinCode }],
        DateOnCard:[
            { required: true,message:'请填写上牌日期' }
        ]
      }
    };
  },
  methods:{
      handleSubmit(){
          let _this =this;
          this.$refs.ValidateForm.validate(valid => {
              if(valid){
                  this.formLabelAlign.ID = this.allRules.ID;
                  CompleteInfor(this.formLabelAlign).then(res => {
                    if (res.data.Success) {
                        _this.$message.success("修改成功！");
                       _this.showUserInfoFlag = false;
                       _this.$emit('getData')
                    } else {
                        _this.$message.error(res.data.Error);
                        _this.showUserInfoFlag = false;
                    }
                })
              }
          })
        //  
      },
      resetFrom(){
          if( this.$refs.ValidateForm){
               this.$refs.ValidateForm.resetFields();
          }
      }
  },
};
</script>

<style lang="less" scope>
    
    .el-dialog{
        top:24% !important;
        background: rgba(38, 38, 38, 0.9);
        h2{
            color: #fff;
        }
        .el-form-item__label{
        color:#fff;
        }
        .el-dialog__title{
            color:#fff;
        }
    }
    .el-dialog--small{
        max-width:40%;
    }
    .formArea{
        width:400px;
        margin:0 auto;
    }

</style>



