<template>
<div>
    <h2 class="pageLogin">CAC - 登录</h2>
    <div class="loginWrap">
        <div class="login">
            <h2>活动管理平台</h2>
            <el-form label-position="left" :rules="formRules" ref="ValidateForm" :model="formLabelAlign">
                <el-form-item prop="UserName"> 
                    <label class="tag" for="">用户名</label>
                    <el-input  v-model="formLabelAlign.UserName"></el-input>
                </el-form-item>
                <el-form-item prop="Password">
                    <label class="tag" for="">密<i style="display:inline-block;width:1em;"></i>码</label>
                    <el-input  v-model="formLabelAlign.Password" type="password"></el-input>
                </el-form-item>
                <el-form-item class="btn">
                    <el-button type="primary" :loading="loading" @click.native.prevent="handleSubmit">登&nbsp;&nbsp;录</el-button>
                </el-form-item>
            </el-form>
        </div>
    </div>
</div>
    
</template>
<script>
import { login } from "@/axios/login.js";
export default {
  data() {
    const validateName = (rule, value, callback) => {
      if (value.length < 1) {
        callback(new Error("请填写用户名"));
      } else {
        callback();
      }
    };
    const validatePass = (rule, value, callback) => {
      if (value.length < 1) {
        callback(new Error("请填写密码"));
      } else if (value.length < 6) {
        callback(new Error("密码不能小于6位"));
      } else {
        callback();
      }
    };
    return {
      loading: false,
      formLabelAlign: {
        Password: "",
        UserName: ""
      },
      formRules: {
        UserName: [
          { required: true, trigger: "blur", validator: validateName }
        ],
        Password: [{ required: true, trigger: "blur", validator: validatePass }]
      }
    };
  },
  mounted: function() {},
  methods: {
    handleSubmit() {
      let _this = this;
      this.$refs.ValidateForm.validate(valid => {
        if (valid) {
          _this.loading = true;
          _this.$store.dispatch("Login", _this.formLabelAlign)
            .then(res => {
              _this.loading = false;
              if (res.data.Success) {
                _this.$message.success("登录成功！");
                _this.$router.push({ path: "/home" });
              } else {
                _this.$message.error(res.data.Error);
              }
            })
            .catch(err => {
              _this.loading = false;
              //   console.log(err)
              this.$message({
                message: "登录失败: 未知的用户名或错误密码或服务器错误！",
                type: "error",
                duration: 5 * 1000
              });
            });
        } else {
          return false;
        }
      });
    }
  }
};
</script>

<style lang="less" scope>
.loginWrap {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 800px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-size: 100% 100%;
  background: rgba(38, 38, 38, 0.6);
  .el-form-item__error {
  position: inherit;
  margin-left: 60px;
  }
}

.login {
  box-sizing: border-box;
  width: 400px;
  padding: 50px 40px;
  h2 {
    font-size: 26px;
    font-weight: normal;
    text-align: center;
    padding: 0 0 40px;
    color: #fff;
    margin: 0;
  }
  .el-form-item {
    margin-bottom: 16px;
    position: relative;
  }
  .el-form-item__content {
    font-size: 0;
    .tag {
      display: inline-block;
      color: #fff;
      margin-right: 10px;
      font-size: 16px;
      width: 50px;
    }
    .el-input {
      font-size: 12px;
    }
  }
  .btn {
    margin-top: 25px;
    .el-form-item__content{
       display:flex;
    }
    .el-button {
      flex:1;
      width: 100%;
      margin-left:60px
    }
  }
  .el-input {
    width: 80%;
    flex: 1;
    box-sizing: border-box;
    background: rgba(255, 255, 255, 0.4);
    border: none;
    input {
      border: none;
    }
  }
}
</style>



