import fetch from '@/util/fecth';


export function login(data) {
    return fetch({
      url: '/CAC/SignUp',
      method: 'post',
      data
    });
  }
  export function CustomerList(params) {
    return fetch({
      url: '/CAC/CustomerList',
      method: 'get',
      params
    });
  }
  export function CompleteInfor(data) {
    return fetch({
      url: '/CAC/CompleteInfor',
      method: 'post',
      data
    });
  }



