<template>
    <div class="handleDate">
      <h2 class="pageLogin">CAC - 数据处理</h2>
          <el-row  class="list_container">
              <el-col :span="24" class="export">
                  <h2 class="title">活动管理平台</h2>
                  <p> <el-button type="primary" class="export_btn" @click="getExcel">导出列表</el-button></p>
              </el-col>
              <el-col :span="24" class="exportList">
                  <el-table :row-class-name="colorRow" :data="exportList" ref="exportList" stripe border height="500" style="width: 100%">
                     <el-table-column prop="SurName" align="center" label="姓" width="100">
                     </el-table-column>
                      <el-table-column prop="Name" align="center" label="名" width="100">
                      </el-table-column>
                      <el-table-column prop="Mobile" align="center" label="手机号" width="">
                      </el-table-column>
                      <el-table-column align="center" label="行驶证" width="">
                        <template slot-scope="scope">
                            <img :src="scope.row.DrivingLicense" alt="" class="driveImg">
                          </template>
                      </el-table-column>
                      <el-table-column prop="Time" align="center" label="提交时间" width="110">
                      </el-table-column>
                      <el-table-column prop="Company" align="center" label="公司名称" width="">
                      </el-table-column>
                      <el-table-column prop="YearLife"  align="center" label="车辆使用年限" width="">
                      </el-table-column>
                      <el-table-column prop="VinCode"   align="center" label="底盘号" width="">
                      </el-table-column>
                      <el-table-column  align="center" label="状态" width="">
                        <template slot-scope="scope">
                            <span>{{scope.row.State}}</span>
                            <p v-if="scope.row.RefuseDetail">({{scope.row.RefuseDetail}})</p>
                          </template>
                      </el-table-column>
                      <el-table-column  align="center" label="" width="">
                          <template slot-scope="scope">
                            <span class="addMsgBtn" @click="addMsgMsk(scope.row)">补充信息</span>
                          </template>
                      </el-table-column>
                  </el-table>
              </el-col>
          </el-row>
          <div class="page" v-if="showPage">
                 <el-pagination background @current-change="handleCurrentChange" :current-page.sync="pageTabs.Page" :page-size="pageTabs.PageSize" layout="prev, pager, next, jumper" :total="TotalItemCount">
            </el-pagination>
          </div>
          <addMsg  ref='addMsg' :allRules="AllRules" @getData="getdata"></addMsg>
    </div>
</template>
<script>
import addMsg from './addMsg'
import {CustomerList} from "@/axios/login.js";

export default {
  components: { addMsg },
  data() {
    
    return {
      AllRules:'',
      colorSet:{
        pasClass:'pass',
        notPassClass:'notPass'
      },
      pageTabs:{
        Page: 1,
        PageSize: 10,
      },
      TotalItemCount: 2,
      exportList: []
       
    };
  },
  computed:{
    showPage(){
      return  Math.ceil(this.TotalItemCount/this.pageSize)>1
    }
  },
  methods: {
    getExcel(){},
    handleCurrentChange(){},
    addMsgMsk(row){
      this.AllRules = row;
      this.$refs.addMsg.showUserInfoFlag = true;
      this.$refs.addMsg.resetFrom()
    },
    colorRow(row,rowIndex){
      if(row.State == '未通过'){
        return this.colorSet.notPassClass
      }else if(row.State == '通过'){
        return this.colorSet.pasClass
      }
    },
    getCustomerListData(){
       CustomerList(this.pageTabs).then(res=>{
        if(res.data.Success){
          this.exportList = res.data.Message;
        }else{
          this.$router.push({path:'/login'})
          // this.$message.error(res.data.Error);
        }
      })
    },
    getdata(){
      this.getCustomerListData()
    }
  },
  mounted(){
    this.getCustomerListData()
  }
};
</script>
<style lang="less" scope>
.pass{
  color:#70ad47;
}
.notPass{
  color:#f00;
}
.list_container{
  margin-top:70px;
}
.handleDate {
  color: #fff;
  box-sizing:border-box;
  margin: 0 50px;
  background: rgba(38, 38, 38, 0.4);
  padding:0 15px 20px;
  .export_btn{
    margin:0 0 20px;
  }
  h2{
    text-align: center;
    font-size: 26px;
    margin:15px 0 5px;
  }
  .export {
    padding: 0 50px;
    
    p {
      text-align: right;
    }
  }
    span.addMsgBtn{
        text-decoration: underline;
        cursor: pointer;
        color:#409EFF
    }
    .driveImg{
      width: 60px; 
      height: 60px;
      margin: 0 auto;
      display: block;
    }
}
</style>




